.panel {
    height: 99vh;
    width: 300px;
    background-color: #293d2b;
}

.scroll {
    border-top: 3px solid white;
    background-color: #293d2b;
    height: 70%;
    overflow-x: hidden;
    overflow-y: auto;
    text-align: center;
    padding: 20px;
}

.inputLog {
    resize: none;
    width: 200px;
    height: 75px;
    font-family: Arial, Helvetica, sans-serif;
    color: white;
    background-color: #293d2b;
    border-style: solid;
    border-color: white;
    border-radius: 10px;
    border-width: thin;
    padding: 5px;
    outline: none;
}

.inputDate {
    width: 70px;
    border: none;
    background-color: #293d2b;
    color: white;
    outline: none;
}

.topPanel {
    text-align: center;
    margin-top: 10px;
    padding: 10px;
    padding-bottom: 0px;
}

.labelPlusBtn {
    margin: 5px;
}

.logLabel {
    margin-right: 5px;
    font-size: 20px;
    font-weight: bold;
    color: white;
}

.inputLog::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: rgb(216, 212, 212);
    opacity: 1; /* Firefox */
}

@media (max-width: 700px) 
{
    .panel {
        height: 500px;
        width: 300px;
        background-color: #293d2b;
        margin-bottom: 80px;
    }
}